* { 
    font-family: Arial, Helvetica, sans-serif;
    color: #343434;
    scroll-behavior: smooth;
    transition: all ease-in-out 0.125s;
}

.App {
    display: flex;
    flex-direction: row;
}

.main {
    display: flex;
    flex-direction: column;
}

.main>div {
    padding: 2rem 5rem;
}